const app = getApp()
export default {
	data() {
		return {
			navList:[],
			imgUrl: '',
			list:[],
			current: '0',
			navId: '',
			page: '1'
		}
	},
	onLoad() {
		this.imgUrl = this.$helper.imgUrl
	},
	onShow() {
		this.list = []
		this.getNav()
	},
	methods: {
		// 选项卡
		getNavList(e,nav_id){
			console.log(e)
			this.current = e
			this.navId = nav_id
			this.list = []
			this.page = '1'
			this.getCourseList()
		},
		getNav(){
			uni.showLoading({
				title:'加载中...',
				icon: 'loading',
				duration: 2000
			})
			uni.request({
				url: `${this.$helper.requestUrl}class/video_classify`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = this.$helper.null2str(res.data)
					console.log(res,'++++++++')
					if(res.status_code == 'ok'){
						this.navList = res.data
						this.navId = res.data[0].id
						console.log(this.navId,'*********************')
						this.getCourseList()
					}
				}
			})
		},
		getCourseList(){
			console.log(this.navId,'video_id')
			uni.showLoading({
				title:'加载中...',
				icon: 'loading',
				duration: 2000
			})
			uni.request({
				url: `${this.$helper.requestUrl}class/getVideoList`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data:{
					video_id: this.navId,
					page: this.page,
					page_size: '10'
				},
				success: res => {
					uni.hideLoading();
					res = this.$helper.null2str(res.data)
					console.log(res,'++++++++')
					if(res.status_code == 'ok'){
						this.list = this.list.concat(res.data.data)
					}
				}
			})
		},
		// 跳转
		getList(e){
			console.log(e)
			uni.navigateTo({
				url: `/pages/indexCourse?id=${e}`
			})
		},
		
	},
	next(){
		this.page ++ ;
		this.getCourseList()
	}

}
